import React, {Component} from 'react';
import '../css/Note.css';

class Note extends Component {
    constructor(props){
        super(props);
        this.state = {
            noteValue: "",
            edit: false,
            editing: false,
            editingIdx: null
        }
        
        this.absent = [" ", " ", " ", " "];
        
        this.changeValue = this.changeValue.bind(this);
        this.save = this.save.bind(this);
        this.cancel = this.cancel.bind(this);
        this.edit = this.edit.bind(this);
        this.remove = this.remove.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.createdDate = this.createdDate.bind(this);
        this.dayInfo = this.props.dayInfo || {
                memory: {
                    notes: [],
                    tasks: [],
                    ideas: [],
                    dictionary: []
                },
                dates: {
                    notes: [],
                    tasks: [],
                    ideas: [],
                    dictionary: []
                }
            }
    }
    changeValue(e){
        this.setState({noteValue: e.target.value});
    }
    handleClick(){
        this.setState({edit: true});
    }
    save(){
        let date = new Date(),
            hours = date.getHours() < 10 ? "0"+date.getHours() : date.getHours(),
            mins = date.getMinutes() < 10 ? "0"+date.getMinutes() : date.getMinutes(),
            secs = date.getSeconds() < 10 ? "0"+date.getSeconds() : date.getSeconds(),
            time = `${hours}:${mins}:${secs}`;
        
        if(this.state.noteValue === ""){
            window.alert(" ");
        }else if(this.state.editing){
            let arr = this.props.pageNotes;
            arr[this.state.editingIdx] = this.state.noteValue;
            this.dayInfo.memory[this.props.pageIdx] = arr;
            this.props.setNotes(this.dayInfo.memory, this.dayInfo.dates);
            localStorage.setItem(this.props.today, JSON.stringify(this.dayInfo));
            this.setState({noteValue: "", edit: false, editing: false});
        }else{
            this.dayInfo.memory[this.props.pageIdx].push(this.state.noteValue);
            this.dayInfo.dates[this.props.pageIdx].push(`${this.props.day}.${this.props.month}.${this.props.year} ${time}`);
            localStorage.setItem(this.props.today, JSON.stringify(this.dayInfo));
            this.props.setNotes(this.dayInfo.memory, this.dayInfo.dates);
            this.props.upd([this.props.clickedData.day, this.props.clickedData.month, this.props.clickedData.year]);
            this.setState({noteValue: "", edit: false, editing: false});
        }
    }
    cancel(){
        this.setState({noteValue: "", edit: false});
    }
    edit(e){
        this.setState({edit: true, noteValue: this.props.pageNotes[e.target.dataset.edit], editing: true, editingIdx: e.target.dataset.edit});
    }
    createdDate(){
        let date = new Date()
    }
    remove(e){
        let conf = window.confirm(" ?");
        if(conf){
            let arr = this.props.pageNotes.slice(),
                dateArr = this.props.pageDates.slice(),
                active = this.props.active.slice(),
                activeStr = active.map(el => el.join("."));
            arr.splice(e.target.dataset.del, 1);
            dateArr.splice(e.target.dataset.del, 1);
            this.dayInfo.memory[this.props.pageIdx] = arr;
            this.dayInfo.dates[this.props.pageIdx] = dateArr;
            this.props.setNotes(this.dayInfo.memory, this.dayInfo.dates);
            localStorage.setItem(this.props.today, JSON.stringify(this.dayInfo));
            
            let count = 0;
            for(let key in this.dayInfo.memory){
                if(this.dayInfo.memory[key].length === 0) count++;
            }
            if(count === Object.keys(this.dayInfo.memory).length) {
                active.splice(activeStr.indexOf(this.props.today), 1);
                console.log(active);
                this.props.upd(active, true);
            }
        }
    }
    render(){
        if(!this.state.edit && this.props.pageNotes.length > 0){
            return (
                <div className="NotesWrapper">
                    {this.props.pageNotes.map((el, i) => {
                        return (
                            <div className="NoteWrapper" key={i}>
                                <div className="Note">
                                    <p>{this.props.pageNotes[i]}</p>
                                </div>
                                <div className="NoteControls">
                                    <button className="NoteControlsEdit" onClick={this.edit} data-edit={i}></button>
                                    <button className="NoteControlsRemove" onClick={this.remove} data-del={i}></button>
                                </div>
                                <div className="NoteDate">
                                    <p>{this.props.pageDates[i]}</p>
                                </div>
                            </div>
                        );
                    })}
                    <button className="NoteAdd" onClick={this.handleClick}> </button>
                </div>
            );
        }else if(this.state.edit){
            return (
                <div className="NoteEdit">
                    <textarea className="memo" onChange={this.changeValue} value={this.state.noteValue}></textarea>
                    <div className="NoteBtns">
                        <button onClick={this.save}></button>
                        <button onClick={this.cancel}></button>
                    </div>
                </div>
            );
        }else if(this.props.pageNotes.length === 0){
            return (
                <div className="NoNote">
                    <h2>{this.absent[this.props.absent]}, ?</h2>
                    <button className="submit" onClick={this.handleClick}></button>
                </div>
            );
        }
    }
}

export default Note


