import React, {Component} from 'react';
import Calendar from './Calendar.jsx';
import Note from './Note.jsx';
import '../css/Content.css';

class Content extends Component {
    constructor(props){
        super(props);
        this.today = this.props.today;
        this.state = {
            memory: {
                notes: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).memory.notes : [],
                tasks: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).memory.tasks : [],
                ideas: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).memory.ideas : [],
                dictionary: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).memory.dictionary : []
            },
            dates: {
                notes: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).dates.notes : [],
                tasks: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).dates.tasks : [],
                ideas: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).dates.ideas : [],
                dictionary: JSON.parse(localStorage.getItem(this.today)) !== null ?  JSON.parse(localStorage.getItem(this.today)).dates.dictionary : []
            },
            clickedData: {
                day: null,
                month: null,
                year: null
            },
            calYear: null,
            calMonth: null,
            calMonthArr: null,
            calDay: null,
            calTime: null,
            calInitYear: null,
            calInitMonth: null,
            noteVisible: false,
            active: JSON.parse(localStorage.getItem("active")) || []
        }
        this.arrayState = Object.keys(this.state.memory);
        this.memoryCopy = this.state.memory;
        this.datesCopy = this.state.dates;
        this.clickedDataCopy = this.state.clickedData;
        this.memoryValue = null;
        this.datesValue = null;
        this.handleClick = this.handleClick.bind(this);
        this.updateData = this.updateData.bind(this);
        this.onceUpd = this.onceUpd.bind(this);
        this.setNote = this.setNote.bind(this);
        this.setDate = this.setDate.bind(this);
        this.updClickedData = this.updClickedData.bind(this);
        this.memoryIdx = null;
        this.dayInfo = null;
        this.emptyMem = {
            notes: [],
            tasks: [],
            ideas: [],
            dictionary: []
        };
        this.emptyDates = {...this.emptyMem};
        this.updActive = this.updActive.bind(this);
    }
    handleClick(idx){
        this.setState({noteVisible: true})
    }
    setNote(memory, dates){
//        this.memoryCopy[this.memoryIdx].push(value);
        this.dayInfo = {
            memory: {...memory},
            dates: {...dates}
        };
        this.memoryCopy = {...memory};
        this.datesCopy = {...dates};
        this.setState({
            memory: {...memory},
            dates: {...dates}
        })
    }
    setDate(obj){
//        this.datesCopy[this.memoryIdx].push(value);
        this.dayInfo = {};
        this.dayInfo.dates = {...obj.dates};
        this.setState({
            dates: {...obj.dates}
        })
    }
    onceUpd(day, time, initMonth, initYear, arr, today){
        this.today = today;
        this.setState({calMonthArr: arr, calDay: day, calTime: time, calInitMonth: initMonth, calInitYear: initYear})
    }
    updateData(year, month){
        this.setState({calYear: year, calMonth: month});
    }
    updClickedData(...dates){
        let i = 0;
        for(let key in this.clickedDataCopy){
            dates[i] = dates[i] < 10 && i !== 2 ? "0"+dates[i] : dates[i];
            this.clickedDataCopy[key] = dates[i];
            i++;
        }
        this.today = `${this.state.clickedData.day}.${this.state.clickedData.month}.${this.state.clickedData.year}`;
        this.setState({
            clickedData: this.clickedDataCopy,
            memory: JSON.parse(localStorage.getItem(this.today)) !== null ? JSON.parse(localStorage.getItem(this.today)).memory : this.emptyMem,
            dates: JSON.parse(localStorage.getItem(this.today)) !== null ? JSON.parse(localStorage.getItem(this.today)).dates : this.emptyDates
        });
    }
    updActive(arr, reset){
        let activeStr = this.state.active.map(el => el.join("."));
        if(reset) {
            localStorage.setItem("active", JSON.stringify(arr));
            this.setState({active: arr});
        }
        else{
            if(activeStr.indexOf(this.today) !== -1) return;
            localStorage.setItem("active", JSON.stringify([...this.state.active, arr]));
            this.setState({active: [...this.state.active, arr]});
        }
    }
    render(){
        let page = this.props.array[this.props.page],
            memoryIdx = this.arrayState[this.props.page-1],
            memoryValue = this.state.memory[memoryIdx],
            datesValue = this.state.dates[memoryIdx];
        this.memoryValue = memoryValue;
        this.datesValue = datesValue;
        this.memoryIdx = memoryIdx;
        switch(page){
            case "":
                return (
                    <div className="Content">
                       <div className="page">
                           <h1>{page}</h1>
                       </div>
                        <Calendar upd={this.updateData} lastMonth={this.state.calMonth} lastYear={this.state.calYear} onceUpd={this.onceUpd} toNotePage={this.props.upd} updClickedData={this.updClickedData} active={this.state.active.map(el => el.map(el => +el))} day={this.state.clickedData.day}/>
                    </div>
                );
            default:
                return (
                    <div className="Content">
                        <div className="page">
                            <h1>{page}  {this.today}</h1>
                        </div>
                        <div className="edit">
                            <Note visible={this.state.noteVisible} pageNotes={memoryValue} setNotes={this.setNote} onBtnClick={() => this.handleClick(memoryIdx)} pageIdx={this.memoryIdx} year={this.state.calInitYear} month={this.state.calInitMonth < 10 ? "0"+(+this.state.calInitMonth+1) : this.state.calInitMonth+1} day={this.state.calDay} pageDates={datesValue} setDate={this.setDate} absent={this.props.page-1} today={this.today} dayInfo={JSON.parse(localStorage.getItem(this.today))} clickedData={this.state.clickedData} upd={this.updActive} active={this.state.active} />
                        </div>
                    </div>
                );
        }
    }
}

export default Content


