import React, {Component} from 'react';
import '../css/Calendar.css';

class Calendar extends Component {
    constructor(props){
        super(props);
        this.date = new Date();
        this.monthIdx = this.props.lastMonth || this.date.getMonth();
        this.year = this.props.lastYear || this.date.getFullYear();
        this.initYear = this.date.getFullYear();
        this.initMonth = this.date.getMonth();
        this.initDay = this.date.getDate();
        this.time = `${this.date.getHours()}:${this.date.getMinutes()}:${this.date.getSeconds()}`;
        this.today = this.getToday();
        this.monthArr = ["", "", "", "", "", "", "", "", "", "", "", ""];
        this.engMonth = ["Jan", "Feb", "March", "Apr", "May", "June", "July", "Aug", "Sept", "Oct", "Nov", "Dec"];
        this.daysArr = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
        this.daysInMonth = this.daysArr[this.monthIdx];
        this.children = [];
        this.selectedDay = this.initDay;
        
        this.state = {
            month: this.monthArr[this.monthIdx],
            year: this.year
        }
        
        this.getDay = this.getDay.bind(this);
        this.setMonth = this.setMonth.bind(this);
        this.setYear = this.setYear.bind(this);
        this.nextYear = this.nextYear.bind(this);
        this.prevYear = this.prevYear.bind(this);
        this.drawDays = this.drawDays.bind(this);
        this.prev = this.prev.bind(this);
        this.next = this.next.bind(this);
        this.selectDay = this.selectDay.bind(this);
        this.getToday = this.getToday.bind(this);
        this.activeDays = this.activeDays.bind(this);
    }
    getDay(day){
        switch(day){
            case 0:
                return 6;
            default:
                return day-1;
        }
    }
    getToday(day, month, year){
        day = this.initDay < 10 ? "0"+this.initDay : this.initDay;
        month = this.initMonth+1 < 10 ? "0"+(this.initMonth+1) : this.initMonth;
        year = this.initYear;
        return `${day}.${month}.${year}`;
    }
    activeDays(...args){ // day, month, year
        let cond = false;
        this.props.active.forEach(arr => {
            let res = arr.filter((el, i) => el === args[i]);
            if(res.length === 3) cond = true;
        })
        return cond;
    }
    prev(){
        this.setMonth(this.monthIdx-1 % this.monthArr.length < 0 ? this.monthIdx = this.monthArr.length-1 : --this.monthIdx % this.monthArr.length);
        this.prevYear();
        this.drawDays();
    }
    next(){
        this.setMonth(++this.monthIdx % this.monthArr.length);
        this.nextYear();
        this.drawDays();
    }
    setMonth(idx){
        this.monthIdx = idx;
        this.daysInMonth = this.daysArr[this.monthIdx];
        this.setState({month: this.monthArr[idx]});
    }
    setYear(year){
        this.year = year;
        this.year % 4 === 0 ? this.daysArr[1] = 29 : this.daysArr[1] = 28;
        this.setState({year: year});
    }
    nextYear(){
        if(this.monthIdx === 0) this.setYear(this.year+1);
    }
    prevYear(){
        if(this.monthIdx === this.monthArr.length-1) this.setYear(this.year-1);
    }
    selectDay(e){
        this.selectedDay = e.target.innerHTML;
        this.props.toNotePage(1);
        this.props.updClickedData(this.selectedDay, this.monthIdx+1, this.year);
    }
    drawDays(){
        let start = this.getDay(new Date(this.engMonth[this.monthIdx] + "1" + this.year).getDay()),
            td = [];
        this.children = [];
        let day = 1,
            idx = 0,
            rowsNum = Math.ceil(this.daysInMonth/7);
        if(this.daysInMonth === 28 && start > 0) rowsNum++;
        if(this.daysInMonth === 29) rowsNum++;
        if(start > 4){
            if(this.daysInMonth === 31) rowsNum++;
            else if(start > 5) rowsNum++;
        }
        
        for(let i = 0; i < rowsNum; i++){
            for(let j = 0; j < 7; j++){
                if(idx >= start) td.push(<td key={2000+day+j} className={day === this.initDay && this.state.month === this.monthArr[this.initMonth] && this.state.year === this.initYear ? "currDay" : null} onClick={e => e.target.innerHTML !== "" ? this.selectDay(e) : null} style={this.activeDays(day, this.monthIdx+1, this.year) ? {background: "#ccc"} : null}>{day < this.daysInMonth+1 ? day++ : ""}</td>)
                else td.push(<td key={3000+j}></td>)
                idx++;
            }
            this.children.push(<tr key={4000+day+i}>{td}</tr>)
            td = [];
        }
        return this.children;
    }
    componentDidMount(){
        this.props.onceUpd(this.initDay, this.time, this.initMonth, this.initYear, this.monthArr, this.today);
        this.props.updClickedData(this.initDay, this.monthIdx+1, this.initYear);
    }
    componentWillUnmount(){
        this.props.upd(this.state.year, this.monthIdx);
    }
    render(){
        return (
            <div id="calendar">
                <div className="controls">
                    <div className="info">
                        <p className="year">{this.state.year}</p>
                        <p className="month">{this.state.month}</p>
                    </div>
                    <div className="btns">
                        <button onClick={this.prev}>&lt;</button>
                        <button onClick={this.next}>&gt;</button>
                    </div>
                </div>
                <div className="days">
                    <table>
                       <tbody>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th className="red"></th>
                                <th className="red"></th>
                            </tr>
                            {this.drawDays()}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Calendar


