import React, {Component} from 'react';
import Content from './Content.jsx';
import '../css/Navigation.css';

const btns = ["", "", "", "", ""];

class Navigation extends Component {
    constructor(props){
        super(props);
        this.state = {
            initPageIdx: 0, // 
            red: 0
        }
        this.handleClick = this.handleClick.bind(this);
        this.updatePage = this.updatePage.bind(this);
        this.today = this.getToday();
        this.getToday = this.getToday.bind(this);
    }
    handleClick(e){
        this.setState({initPageIdx: e.target.dataset.idx, red: e.target.dataset.idx})
    }
    updatePage(page){
        this.setState({initPageIdx: page, red: page});
    }
    getToday(day, month, year){
        day = new Date().getDate() < 10 ? "0"+new Date().getDate() : new Date().getDate();
        month = new Date().getMonth()+1 ? "0"+(new Date().getMonth()+1) : new Date().getMonth();
        year = new Date().getFullYear();
        return `${day}.${month}.${year}`;
    }
    render(){
        return (
            <div className="Navigation">
                <nav>
                    <ul>
                        {btns.map((el, i) => <li key={i} onClick={this.handleClick} data-idx={i} style={this.state.red == i ? {background: "red"} : {backround: "#1490c1"}}>{el}</li>)}
                    </ul>
                </nav>
                <Content array={btns} page={this.state.initPageIdx} upd={this.updatePage} today={this.today}></Content>
            </div>
        );
    }
}

export default Navigation


